import JsonRPC from './jsonrpc';

export default class {
    public jsonRPC: JsonRPC;
    constructor(jsonRPC: JsonRPC) {
        this.jsonRPC = jsonRPC;
    }

    public async transfer(data: {
        fee: number,
        amount: number,
        message?: string,
        to: string,
        from: string,
        encrypted?: number
    }) {
        return await this.jsonRPC.post('/api/v2/serialize/transfer', data);
    }

    public async lease(data: {
        fee: number,
        amount: number,
        to: string,
        from: string
    }) {
        return await this.jsonRPC.post('/api/v2/serialize/lease', data);
    }

    public async cancelLease(data: {
        fee: number,
        amount: number,
        to: string,
        height: number,
        from: string
    }) {
        return await this.jsonRPC.post('/api/v2/serialize/cancellease', data);
    }
}
